import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import './InvitadoHome.css';

export default class InvitadoHome extends Component {
    render() {
        return (
            <main>
                <div className="container allHome">
                    <div className="row">
                        <div className="col-md-4 text-center mt-5">
                            <h2 className="texto">Comunidad de programadores CODIGO 9</h2>
                            <p className="texto mt-3">Plataforma educativa de libre acceso</p>
                            <p className="texto">Si estas cansado de intentos fallidos de éxito en programación, esta es la oportunidad que estabas buscando, aprovechala. </p>
                            <Link type="button" className="btn btn-danger" to="/invitado">Empezar ahora</Link>
                        </div>
                        <div className="col-md-4"></div>
                        <div className="col-md-4"></div>
                    </div>
                </div>
            </main>
        )
    }
}
