import React, { Component } from 'react'

export default class Recuperar extends Component {
    render() {
        return (
            <div className="container">
            <div className="row">
                <div className="col-md-4"></div>
                <div className="col-md-4">
                    <h3 className="text-center mt-5">Recuperar Contraseña</h3>
                    <form className="mt-5">
                        <div className="form-group">
                            <label htmlFor="usuario">Ingrese Correo</label>
                            <input className="form-control" type="email" id="usuario"/>
                        </div>
                        <button className="btn btn-success">Solicitar</button>
                    </form>
                </div>
                <div className="col-md-4"></div>
            </div>
        </div>
        )
    }
}
