import React, { Component } from 'react'
import { Fragment } from 'react'
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom'
import Invitado from './modulos/invitado/Invitado'
import Loggin from './modulos/loggin/Loggin'
import Header from './componentes/Header'
import Administrador from './modulos/administrador/Administrador'
import Cliente from './modulos/cliente/Cliente'

export default class App extends Component {
  render() {
    return (
      <Fragment>
        <Router>
          <Header />
          <Switch>
            <Route path={"/invitado"} render={() => { return <Invitado /> }} />
            <Route path={"/loggin"} render={() => { return <Loggin /> }} />
            <Route path={"/admin"} render={()=>{return <Administrador/>}}/>
            <Route path={"/cliente"} render={()=>{return <Cliente/>}}/>
            <Route render={() => { return <Redirect to={"/invitado"} /> }} />
          </Switch>
        </Router>
      </Fragment>
    )
  }
}
