import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class Logeo extends Component {
    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-md-4"></div>
                    <div className="col-md-4">
                        <h3 className="text-center mt-5">Iniciar Sesion</h3>
                        <form className="mt-5">
                            <div className="form-group">
                                <label htmlFor="usuario">Nombre de usuario</label>
                                <input className="form-control" type="text" id="usuario"/>
                            </div>
                            <div className="form-group">
                                <label htmlFor="contrasena">Contraseña</label>
                                <input className="form-control" type="password" id="contrasena"/>
                            </div>
                            <div className="form-group">
                            <Link to={"/loggin/registrar"}>Registrarse</Link>
                            </div>
                            <div className="form-group">
                            <Link to={"loggin/recuperar"}>Recuperar contraseña</Link>
                            </div>
                            <button className="btn btn-success">Iniciar Sesión</button>
                        </form>
                    </div>
                    <div className="col-md-4"></div>
                </div>
            </div>
        )
    }
}
