import React, { Component } from 'react'
import { Fragment } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import InvitadoHome from './paginas/InvitadoHome'
import InvitadoContact from './paginas/InvitadoContact'
import InvitadoProducts from './paginas/InvitadoProducts'

export default class Invitado extends Component {
    render() {
        return (
            <Fragment>
                <Switch>
                    <Route path={"/invitado"} exact render={() => { return <InvitadoHome /> }} />
                    <Route path={"/invitado/contact"} render={() => { return <InvitadoContact /> }} />
                    <Route path={"/invitado/products"} render={() => { return <InvitadoProducts /> }} />
                    <Route render={() => { return <Redirect to={"/invitado"} /> }} />
                </Switch>
            </Fragment>
        )
    }
}
