import React, { Fragment, Component } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import ClienteHome from './paginas/ClienteHome'
import ClienteCompras from './paginas/ClienteCompras'
import ClienteVentas from './paginas/ClienteVentas'

export default class Cliente extends Component {
    render() {
        return (
            <Fragment>
                <Switch>
                    <Route path={"/cliente/"} exact render={() => { return <ClienteHome /> }} />
                    <Route path={"/cliente/compras"} render={() => { return <ClienteCompras /> }} />
                    <Route path={"/cliente/ventas/:id_compra"} render={() => { return <ClienteVentas /> }} />
                    <Route render={()=>{return <Redirect to={"/cliente"}/>}}/>
                </Switch>
            </Fragment>
        )
    }
}
