import React, { Component } from 'react'
import { Fragment } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import Logeo from './paginas/Logeo'
import Register from './paginas/Register'
import Recuperar from './paginas/Recuperar'

export default class Loggin extends Component {
    render() {
        return (
            <Fragment>
                <Switch>
                    <Route path={"/loggin"} exact render={()=>{return <Logeo/>}}/>
                    <Route path={"/loggin/registrar"} render={()=>{return <Register/>}}/>
                    <Route path={"/loggin/recuperar"} render={()=>{return <Recuperar/>}}/>
                    <Route render={()=>{return <Redirect to={"/loggin"}/>}}/>
                </Switch>
            </Fragment>
        )
    }
}
