import React, { Component, Fragment } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import AdminDashboard from './paginas/AdminDashboard'
import AdminPerfil from './paginas/AdminPerfil'

export default class Administrador extends Component {
    render() {
        return (
            <Fragment>
                <Switch>
                    <Route path={"/admin"} exact render={()=>{return <AdminDashboard/>}}/>
                    <Route path={"/admin/dashboard"} render={()=>{return <AdminDashboard/>}}/>
                    <Route path={"/admin/profile"} render={()=>{return <AdminPerfil/>}}/>
                    <Route render={()=>{return <Redirect to={"/admin"}/>}}/>
                </Switch>
            </Fragment>
        )
    }
}
