import React, { Component } from 'react'

export default class AdminPerfil extends Component {
    render() {
        return (
            <div className="container mt-5">
                <div className="row">
                    <div className="col-md-6">
                        <div className="card shadow">
                            <div className="card-body">
                                <form>
                                    <div className="form-group">
                                        <label htmlFor="nombrePerfil">Nombres:</label>
                                        <span id="nombrePerfil" className="form-control">Paolo Soncco</span>
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="nombrePerfil">Rol:</label>
                                        <span id="nombrePerfil" className="form-control">Encargado de contabilidad</span>
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="nombrePerfil">Correo:</label>
                                        <span id="nombrePerfil" className="form-control">paolosoncco@gmail.com</span>
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="nombrePerfil">Telefono:</label>
                                        <span id="nombrePerfil" className="form-control">+51 987654321</span>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="card shadow">
                            <div className="card-body mx-auto">
                                <img height="350px" src="http://www.unfv.edu.pe/facultades/fiei/images/cono_a.png" alt="Foto de perfil"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
