import React, { Component } from 'react'

export default class InvitadoContact extends Component {
    render() {
        // console.log(this.enviado);
        return (         
            <div>
                <div className="row">
                    <div className="col-md-4"></div>
                    <div className="col-md-4">
                        <form>
                            <div className="form-group">
                                <label htmlFor="correo">Ingrese Correo</label>
                                <input className="form-control" type="email" id="correo" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="datosPersonales">Nombre</label>
                                <input className="form-control" type="text" id="datosPersonales" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="mensaje">Mensaje</label>
                                <textarea className="form-control" id="mensaje" rows="3"></textarea>
                            </div>
                            <button className="btn btn-danger">Enviar</button>
                        </form>
                    </div>
                    <div className="col-md-4"></div>
                </div>
            </div>
        )
    }
}
